export function calcRawDiets(
  polandTime,
  abroadTime,
  dietValue,
  currencyRate,
  breakfastPL,
  dinnerPL,
  supperPL,
  breakfastAbroad,
  dinnerAbroad,
  supperAbroad
) {
  let result = {
    dietRawPl: 0,
    dietPL: 0,
    dietRawAbroad: 0,
    dietAbroad: 0,
    dietRawAbroadInPLN: 0,
    dietAbroadInPLN: 0,
    dietSumup: 0,
  };
  let sumPlTime = 0;
  let sumAbroadTime = 0;
  //Calculating diet for Poland
  polandTime.forEach((element) => {
    sumPlTime += element;
  });
  //Calculating diet abraod
  abroadTime.forEach((element) => {
    sumAbroadTime += element;
  });

  let dietRawPL = sumPlTime * 30;
  let dietPL = parseFloat(
    (
      dietRawPL -
      ((parseInt(breakfastPL) + parseInt(supperPL)) * 0.15 +
        0.5 * parseInt(dinnerPL)) *
        30
    ).toFixed(2)
  );
  let dietRawAbroad = parseFloat((sumAbroadTime * dietValue).toFixed(2));
  let dietAbroad = (
    dietRawAbroad -
    ((parseInt(breakfastAbroad) + parseInt(supperAbroad)) * 0.15 +
      0.5 * dinnerAbroad) *
      dietValue
  ).toFixed(2);

  let dietRawAbroadInPLN = parseFloat(
    (dietRawAbroad * currencyRate).toFixed(2)
  );
  let dietAbroadInPLN = parseFloat((dietAbroad * currencyRate).toFixed(2));
  let dietSumup = dietPL + dietAbroadInPLN;
  console.log(
    ((parseInt(breakfastAbroad) + parseInt(supperAbroad)) * 0.15 +
      0.5 * dinnerAbroad) *
      dietValue
  );


  result.dietRawPl = dietRawPL;
  result.dietPl = dietPL;
  result.dietRawAbroad = dietRawAbroad;
  result.dietAbroad = dietAbroad;
  result.dietRawAbroadInPLN = dietRawAbroadInPLN;
  result.dietAbroadInPLN = dietAbroadInPLN;
  result.dietSumup = dietSumup;

  
  
 

  return result;
}

export function calcFinalCosts(dietSumup, expensesArray) {
 
  let finalCosts = {
    total: 0,
    employeeCosts: 0,
    campanyCardCosts: 0,
    campanyTransfer: 0,
    advanceCost: 0,
    campanyReturnCosts: 0,
    finalRefund:0
  };
  let employeeExpenses=0;
  expensesArray.forEach((element) => {
    console.log(element);
    if (element.paymentOption === "Pracownik") {
      employeeExpenses += parseFloat(element.totalCost);
    } else if (element.paymentOption === "Karta firmowa") {
      finalCosts.campanyCardCosts += parseFloat(element.totalCost);
    } else if (element.paymentOption === "Przelew") {
      finalCosts.campanyTransfer += parseFloat(element.totalCost);
    } else if (element.type === "Zaliczka") {
      finalCosts.advanceCost += parseFloat(element.totalCost);
    }
  });
  finalCosts.total =
  parseFloat((employeeExpenses +
    finalCosts.campanyCardCosts +
    finalCosts.campanyTransfer +
    finalCosts.advanceCost +
    dietSumup).toFixed(2));
    finalCosts.employeeCosts =(employeeExpenses+ dietSumup).toFixed(2) 

  
    finalCosts.finalRefund= (finalCosts.employeeCosts- finalCosts.advanceCost).toFixed(2);
    return finalCosts;
}
