import React, { Component } from "react";
export default (props) => {
  return (
    <div className="size-width-100">
      <table className="calculator-date-table">
        <thead>
          <tr ><th colspan="3" >{props.title}</th></tr>
        </thead>
        <tbody>
          <tr>
            <td>Liczba śniadań:</td>
            <td>Liczba obiadów:</td>
            <td>Liczba kolacji:</td>
          </tr>
          <tr>
            <td>
              <input className="size-width-100"
                type="number"
                min="0"
                step="1"
                placeholder="0"
                name={props.breakfast}
                onChange={props.handleChange}
              />
            </td>
            <td>
              <input className="size-width-100"
                type="number"
                min="0"
                step="1"
                placeholder="0"
                name={props.dinner}
                onChange={props.handleChange}
              />
            </td>
            <td>
              <input className="size-width-100"
                type="number"
                min="0"
                step="1"
                placeholder="0"
                name={props.supper}
                onChange={props.handleChange}
              />
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};
